import React, { useState } from "react";
import Lightbox from "react-image-lightbox";
import "react-image-lightbox/style.css"; // Import the styles for the lightbox

import { GatsbyImage, getImage } from "gatsby-plugin-image";


const ImageGallery = ({ images }) => {
  const [lightboxOpen, setLightboxOpen] = useState(false);
  const [photoIndex, setPhotoIndex] = useState(0);

  const openLightbox = () => {
    setLightboxOpen(true);
  };

  const closeLightbox = () => {
    setLightboxOpen(false);
  };

  const imageCount = images.length;

  return (
    <div>
      {/* Display the first image as the header picture */}
      <div onClick={openLightbox} style={{ cursor: "pointer", position: "relative" }}>
        <img
          src={images[0].childImageSharp.gatsbyImageData.src}
          alt={`Gallery Image 1`}
        />
        <div
          style={{
            position: "absolute",
            bottom: "5px",
            right: "5px",
            backgroundColor: "rgba(0, 0, 0, 0.7)",
            color: "white",
            padding: "5px",
            borderRadius: "5px",
          }}
        >
          {imageCount > 1 ? `${imageCount} Fotos` : ""}
        </div>
      </div>

      {/* Lightbox component */}
      {lightboxOpen && (
        <Lightbox
          mainSrc={images[photoIndex].childImageSharp.gatsbyImageData.src}
          nextSrc={
            images[(photoIndex + 1) % imageCount].childImageSharp.gatsbyImageData.src
          }
          prevSrc={
            images[
              (photoIndex + imageCount - 1) % imageCount
            ].childImageSharp.gatsbyImageData.src
          }
          onCloseRequest={closeLightbox}
          onMovePrevRequest={() =>
            setPhotoIndex((photoIndex + imageCount - 1) % imageCount)
          }
          onMoveNextRequest={() =>
            setPhotoIndex((photoIndex + 1) % imageCount)
          }
        />
      )}
    </div>
  );
};

export default ImageGallery;
